.editor {
  width: 80vw;
  margin-bottom: 10vh;
  color: inherit;
  font-family: inherit;

  &__items-list {
    width: 20vw;
    height: 400px;
    border-bottom-left-radius: 5px;
    border-top-left-radius: 5px;
    overflow-y: scroll;
    position: absolute;

    &::-webkit-scrollbar {
      width: 5px;
    }

    &::-webkit-scrollbar-thumb {
      border-radius: 10px;
      -webkit-box-shadow: inset 0 0 3px rgba(0, 0, 0, .4);
    }

    #p-1, #p-2, #p-3, #p-4, #p-5, #p-6, #p-7 {
      background-color: #f8f9f0;
      font-size: 2vmin;

      .MuiExpansionPanelSummary-root {
        height: 6vh;
      }

      .MuiButtonBase-root {
        color: #54605a;
      }

      .MuiExpansionPanelDetails-root {
        display: block;
        img {
          cursor: pointer;
          display: block;
          margin: auto;
          margin-bottom: 10px;
          transform: scale(0.7);
        }
      }
    }

    #p-1 .MuiExpansionPanelDetails-root img {
      margin-left: -12%;
      transform: scale(0.5);
    }

  }

  &__canvas {
    margin-left: 20vw;
    width: 750px;
    height: 400px;
    background: rgba(108,154,34, 0.3) url("../../resources/bcg-2.png") 97% 103%;
    border: 1px solid white;
    border-bottom-right-radius: 5px;
    border-top-right-radius: 5px;
    cursor: pointer;
  }

  > button {
    margin-left: 67.5vw;
    margin-bottom: 2vh;
    background-color: #f8f9f0;
    padding: 1vmin;
    border-radius: 10px;
    color: inherit;
    font-family: inherit;
    cursor: pointer;
    font-size: 2vmin;

    border: 1px solid rgba(108,154,34, 0.3);
  }
}
